.menu_container {
    width: 100%;
    display: grid;
    grid-template-columns: 0.2fr 1fr;
    justify-items: center;
    gap: 20px;
  }

  .menu_container_right {
    flex: 1;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 20px;
  }

  .menu_container_title_container {
    width: 100%;
    display: flex;
    align-items: center;
    gap: 20px;
    justify-content: space-between;
  }

  .menu_title_bars {
    height: 100%;
    flex: 1;
    content: " ";
    display: block;
    position: relative;
    /* background-color: yellow ; */
  }
  .menu_title_bars::before {
    content: " ";
    display: block;
    height: 2px;
    width: 100%;
    background-color: #ff9b55;
    position: absolute;
    top: 25%;
    left: 0%;
  }
  .menu_title_bars::after {
    content: " ";
    display: block;
    height: 2px;
    width: 100%;
    background-color: #ff9b55;
    position: absolute;
    bottom: 25%;
    left: 0%;
  }

  @media screen and (max-width: 768px) {
    .menu_container {
      grid-template-columns: 1fr;
    }
  }