.events_main_container {
  padding: var(--paddng_type-2);
  width: 100%;
  background-color: #0E0D0C;
}

.events_main_inner_container {
  height : 100%;
  width: 100%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-inline: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
}

.events_main_bottom_container {
  width: 100%;
  height : 100%;
  display: grid;
  padding: 3rem 0rem;
  margin-left: auto;
  margin-right: auto;
  column-gap: var(--gap_type-1);
  row-gap: var(--gap_type-1);
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  justify-items: center;
}

@media screen and (max-width: 768px) {
  .event_main_bottom_container {
    grid-template-columns: 1fr;
  }

  .event_container:nth-child(3){
    width : 100%;
  }

}

@media screen and (max-width : 900px) {
  .events_main_bottom_container{
    grid-template-columns: 1fr;
  }
  .event_container:nth-child(3){
    width : calc(50% - 20px);
    grid-column: 1/3;
  }
}
.event_container {
  position: relative;
  overflow: hidden;
  cursor: pointer;
  width: 100%;
  max-width: 400px;
  height: 100%;
}

.event_image_container {
  width: 100%;
  height: 100%;
}

.event_image_container img {
  width: 100%;
  height: 100%;
  transition: 0.2s linear;
}

.event_data {
  position: absolute;
  top: 4%;
  left: 4%;
}

.event_p_type_3 {
  color: #ff9b55;
  padding: 0.5rem 0.5rem;
  background-color: black;
}

.event_p_type_1 {
  color: #fff1e7;
  font-size: 16px;
  font-family: "Forum";
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-shadow: 0px 0px 10px #ffffff;
}

.event_p_type_2 {
  color: #ffffff;
  font-size: 24px;
  font-weight: bold;
  letter-spacing: 2px;
  font-family: "roboto";
  padding-top: 10px;
  text-shadow: 0px 0px 5px #ff8f3e;
}

.event_title_container {
  width: 100%;
  position: absolute;
  bottom: 5%;
  left: 0%;
  padding: 0rem 3rem;
}

.event_main_blog_view_button{
  padding-block-end : 5rem;
}

.event_container::before {
  width: 50%;
  height: 200%;
  content: " ";
  display: block;
  background-image: linear-gradient(
    -90deg,
    rgba(255, 255, 255, 0.745),
    rgba(255, 255, 255, 0)
  );
  background-size: 50% 100%;
  background-repeat: no-repeat;
  transform-origin: left;
  visibility: hidden;
  position: absolute;
  z-index: 3;
  /* visibility: hidden; */
}

.event_container:hover::before {
  animation: slide_animate_2 0.6s linear forwards;
}

.event_container:hover .event_image_container img {
  scale: 1.2;
  transition: 0.2s linear;
}

@keyframes slide_animate_2 {
  0% {
    visibility: visible;
    transform: translate(-200%, -50%) rotate(45deg);
  }
  100% {
    transform: translate(200%, 0%) rotate(45deg);
  }
}

@media screen and (max-width: 1030px) {
  .event_container:last-child {
    width: calc(50% - 20px);
    grid-column: 1/3;
  }
}

@media screen and (max-width: 768px) {
  .event_container:last-child {
    grid-column: 1;
    width: 100%;
  }
}