* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html{
  scroll-behavior: smooth;
}

@font-face {
  font-family: "roboto";
  src: url("/src/assets/Fonts/roboto.ttf");
  font-weight: 200;
}

@font-face {
  font-family: "calligraphed";
  src: url("/src/assets/Fonts/PlayfairDisplay-Italic-VariableFont_wght.ttf");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Forum";
  src: url("/src/assets/Fonts/Forum-Regular.ttf");
  font-weight: 50;
}

body {
  width: 100%;
}

#root {
  width: 100%;
  height: 100%;
  overflow: visible;
}

#root.active {
  height: 100vh;
  overflow: hidden;
}

:root {
  --background_color_1: rgba(3, 10, 34, 0.829);
  --padding_top_type_1: 2rem;
  --paddng_type-2: 5rem 3rem 5rem 3rem;
  --width_type-2: 40%;
  --margin_type-2: 40px;
  --translate_type-1: -160px;
  --gap_type-1: 100px;
  --padding_type-3: 0 0 4rem 3rem;
  --font-type-2: 50px;
  --hp_type-1: rgb(174, 174, 174);
}

::-webkit-scrollbar {
  width: 5px;
  background-color: rgba(3, 10, 34, 0.829);
}

::-webkit-scrollbar-track {
  background-color: black;
}

::-webkit-scrollbar-thumb {
  background-color: #ff9b55;
}

.white_space {
  content: " ";
  display: inline-block;
}

.cl_w {
  color: white;
}

.cl_g {
  color: #ff9b55;
}

.cl_i_g{
  font-family: 'Calligraphed';
  font-weight: bold;
  color : #FF9B55;
}



.hp {
  color: var(--hp_type-1);
  font-style: normal;
  font-size: 1.2rem;
  line-height: 1.5;
}

.hp:hover {
  color: #ff9b55;
}

.w_100 {
  width: 100%;
}

.w_50 {
  width: 50%;
}

.h_100 {
  height: max(55vw, 110vh);
  min-height: 600px;
}

.h_100_maintain {
  height: 100vh;
}

.h_f_40 {
  height: 40px;
}

.gap_5 {
  letter-spacing: 5px;
}

.pad_tb_10 {
  padding: 10px 0 10px 0;
}

.pad_tb_20 {
  padding: 20px 0 20px 0;
}
.h_type_large {
  font-family: "Forum";
  font-size: 6rem;
  letter-spacing: 1px;
  word-spacing: 10px;
  font-weight: 200;
}

.p_type_medium {
  font-family: "Forum";
  font-size: 1.4rem;
  font-weight: 100;
}

.p_type_large {
  font-family: "calligraphed";
  font-size: 2rem;
  letter-spacing: 2px;
}

