.fl_cl_cen_end {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}

.fl_cl_cen {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.ob_cover {
  object-fit: cover;
}

.pd_lr_7 {
  padding: 1rem 5rem 1rem 5rem;
}

.royaltySign {
  width: 100%;
  height: 100%;
  text-align: center;
}

