.font-type_1 {
  font-size: 20px;
  padding: 1rem 0;
  letter-spacing: 2px;
  white-space: nowrap;
  color: #ff9b55;
}

.font-type_2 {
  font-size: 1.2rem;
  line-height: 2;
  word-spacing: 1px;
  letter-spacing: 1.4px;
  font-family: "roboto";
}

.heading_type-2 {
  font-size: 4rem;
  font-family: "Forum";
  font-weight: 200;
  letter-spacing: 1px;
  padding: 1rem 1rem;
}
.heading_type-3 {
  font-size: 3rem;
  font-family: "Forum";
  font-weight: 200;
  letter-spacing: 1px;
  padding: 1rem 1rem;
}

.wd_br_no {
  white-space: nowrap;
}

.offers_bottom {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

.font-type_3 {
  font-size: 2rem;
  font-family: "Forum";
  color: #ff9b55;
  font-weight: 500;
}

.font_type-4 {
  font-size: 50px;
  font-family: "Forum";
  color: white;
  font-weight: 400;
}

.header_font_type_1 {
  font-size: 100px;
  word-spacing: 10px;
  letter-spacing: 7px;
  font-family: "roboto";
  font-weight: bold;
}

@media screen and (max-width: 1200px) {
  .header_font_type_1 {
    font-size: 9vw;
    word-spacing: 4px;
    letter-spacing: 3px;
  }
}

@media screen and (max-width: 768px) {
  .h_100 {
    height: max(30vw, 90vh);
  }
  .pd_lr_7 {
    padding: 1rem 2rem 1rem 2rem;
  }

  .font_type-4 {
    font-size: 36px;
  }
}

@media screen and (max-width: 768px) {
  :root {
    --padding_top_type_1: 1rem;
    --paddng_type-2: 3rem 0rem 0rem 0rem;
    --margin_type-2: 10px;
    --padding_type-3: 0 0 4rem 6rem;
  }

  .font-type_2 {
    font-size: 18px;
    line-height: 1.2;
    word-spacing: 1px;
    letter-spacing: 1px;
    font-family: "roboto";
  }

  .heading_type-2 {
    font-size: 22px;
  }
}

a{
  text-decoration: none;
  color : white;
}

@media screen and (max-width: 992px) {
  :root {
    --translate_type-1: 0px;
  }
}

@media screen and (max-width: 1224px) {
  :root {
    --translate_type-1: 0px;
    --width_type-2: 100%;
    --gap_type-1: 50px;
  }
}