.offers_menu_inner_container {
  width: 100%;
  max-width: 500px;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  position: relative;
  cursor: pointer;
}
.service_image_container {
  width: 100%;
  height: 80%;
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem 1rem;
  box-sizing: border-box;
  z-index: 2;
  overflow: hidden;
}

.offers_menu_inner_container:nth-child(2n + 1) {
  transform: translateY(var(--translate_type-1));
}

.service_image_container::before {
  width: 50%;
  height: 200%;
  content: " ";
  display: block;
  background-image: linear-gradient(
    -90deg,
    rgba(255, 255, 255, 0.745),
    rgba(255, 255, 255, 0)
  );
  background-size: 50% 100%;
  background-repeat: no-repeat;
  transform-origin: left;
  visibility: hidden;
  position: absolute;
  z-index: 3;
}

.offers_menu_inner_container:hover .service_image_container::before {
  transform: rotateY(40deg);
  animation: slide_animate 0.6s linear forwards;
}

@keyframes slide_animate {
  0% {
    visibility: visible;
    transform: translate(-200%, 0%) rotate(45deg);
  }
  100% {
    transform: translate(200%, 0%) rotate(45deg);
  }
}

.service_image {
  width: 90%;
  height: 100%;
  z-index: 2;
  overflow: hidden;
}

.offers_menu_inner_container:hover .service_image img {
  scale: 1.2;
  transition: 0.2s linear;
}

.service_image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 2;
  transition: 0.2s linear;
}

.back_pattern {
  width: 100%;
  height: 100%;
  position: absolute;
  background-image: url("/src/assets/images/img-pattern.svg");
  background-repeat: no-repeat;
  background-position: center;
  z-index: 1;
  transition: 0.3s linear;
}

.service_image_title {
  width: 100%;
  height: 20%;
  padding: 1rem 0rem;
  display: flex;
  align-items: center;
  /* justify-content: space-between; */
  flex-direction: column;
}

.menu_link_container {
  padding: 1rem 1rem;
  position: relative;
  z-index: 4;
}
.menu_link_container::before {
  width: 100%;
  height: 1px;
  content: " ";
  display: block;
  position: absolute;
  transform: scaleX(0);
  background-color: #ff9b55;
  left: 0;
  top: 90%;
  transition: 0.3s linear;
}
.menu_link_container::after {
  width: 100%;
  height: 1px;
  content: " ";
  display: block;
  position: absolute;
  transform: scaleX(0);
  background-color: #ff9b55;
  left: 0;
  top: calc(90% + 3px);
  transition: 0.3s linear;
}

.menu_link_container:hover::before,
.menu_link_container:hover::after {
  transform: scaleX(1);
  transition: 0.3s linear;
}

.menu_link {
  text-decoration: none;
  font-family: "roboto";
  font-size: 1.1rem;
  line-height: 0.1;
  letter-spacing: 1px;
  font-weight: 600;
  color: white;
}

.offers_menu_inner_container:hover .back_pattern {
  transform: rotateY(180deg);
  transition: 0.2s linear;
}

@media screen and (max-width: 1030px) {
  .offers_menu_inner_container:nth-child(3) {
    width: calc(50% - 20px);
    grid-column: 1/3;
  }
}
@media screen and (max-width: 768px) {
  .offers_menu_inner_container:nth-child(3) {
    grid-column: 1;
    width: 100%;
  }
}
