.offers_main_container {
    width: 100%;
    padding: var(--paddng_type-2);
    background-color: #0e0d0c;
    position: relative;
    overflow: hidden;
    z-index: 1;
    box-sizing: border-box;
    overflow : hidden ;
  }

  .offers_inner_container {
    width: 100%;
    max-width: 1200px;
    padding-inline: 16px;
    height: 100%;
    display: flex;
    margin-left: auto;
    margin-right: auto;
    flex-direction: column;
    gap: 50px;
  }

  .offers_content_top {
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .offers_heading {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: var(--margin_type-2);
  }

  .royalty {
    width: 100%;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .offers_desc {
    width: var(--width_type-2);
    text-align: center;
  }

  .offers_bottom_main_container {
    flex: 1;
    display: grid;
    column-gap: var(--gap_type-1);
    row-gap: var(--gap_type-2);
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    justify-items: center;
  }

  @media screen and (max-width: 1200px) {
    .offers_bottom_main_container {
      display: grid;
      grid-template-columns: 1fr;
    }
  }
  .service_shape_1 {
    width: 100%;
    height: 40%;
    max-height: 400px;
    background-image: url("/src/assets/images/shape-1.png");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: left;
    z-index: -1;
    position: absolute;
    bottom: -5%;
    left: -1%;
  }
  .service_shape_2 {
    width: 100%;
    height: 40%;
    max-height: 400px;
    background-image: url("/src/assets/images/shape-2.png");
    background-position: right;
    background-size: contain;
    background-repeat: no-repeat;
    z-index: -1;
    position: absolute;
    top: 0%;
    right: 0%;
  }