.menu_main_container {
    width: 100%;
    position: relative;
    /* height : 100vh ;  */
    background-color: #161718;
    place-content: center;
    overflow: hidden;
    z-index: 1;
  }

  .menu_main_background_1 {
    background-image: url("/src/assets/images/shape-5.png");
    background-repeat: no-repeat;
    background-size: contain;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: -1;
  }
  .menu_main_background_2 {
    background-image: url("/src/assets/images/shape-6.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: right bottom;
    max-height: 400px;
    position: absolute;
    width: 100%;
    height: 100%;
    right: 0;
    bottom: 0;
    z-index: -1;
  }

  .menu_main_inner_container {
    color: rgb(200, 189, 189);
    width: 100%;
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
    grid-template-columns: repeat(2, 1fr);
    padding-inline: 16px;
    display: grid;
    column-gap: var(--gap_type-1);
    row-gap: 50px;
  }

  .menu_main_heading {
    text-align: center;
    padding: 30px 0px;
    padding-top: 50px;
  }

  .menu_button_container {
    padding: 3rem 0;
    text-align: center;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    gap: 20px;
  }

  @media screen and (max-width: 968px) {
    .menu_main_inner_container {
      grid-template-columns: 1fr;
    }
  }