.footer_main_container {
  width: 100%;
  background-image: url("/src/assets/images/footer-bg.jpg");
  background-size: cover;
  padding: 5rem 0rem;
  padding-inline: 16px;
  background-color: #161718;
}

.footer_main_inner_container {
  display: grid;
  max-width: 1200px;
  width: 100%;
  grid-template-columns: 1fr 2fr 1fr;
  gap: 20px;
  margin-inline: auto;
}

.footer_logo {
  padding-block: 20px;
}

.maillink {
  text-decoration: none;
  font-family: "Forum";
  font-size: 1.4rem;
  line-height: 1;
  cursor: pointer;
}

.footer_grid_center {
  background-image: url("/src/assets/images/footer-form-bg.png");
  background-size: cover;
  /* background-repeat: no-repeat; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-block: 100px;
  gap: 25px;
  z-index: 90;
  position: relative;
  box-sizing: border-box;
  padding-inline: 5rem;
}

.mar_back {
  margin-block-start: -18px;
}

.footer_grid_center::before {
  content: " ";
  display: block;
  width: 100%;
  height: 100%;
  background-image: url("/src/assets/images/footer-form-pattern.svg");
  background-size: 20px;
  background-repeat: no-repeat;
  background-position: left;
  position: absolute;
  z-index: -1;
}
.footer_grid_center::after {
  content: " ";
  display: block;
  width: 100%;
  height: 100%;
  background-image: url("/src/assets/images/footer-form-pattern.svg");
  background-size: 20px;
  background-repeat: no-repeat;
  background-position: right;
  position: absolute;
  z-index: -1;
}

.subscribe_container {
  width: 100%;
}
.subscribe_container form {
  width: 100%;
}

.subscribe {
  display: grid;
  grid-template-columns: 1fr 0.5fr;
}

.subscribe_button input {
  cursor: pointer;
  z-index: 10;
}

.footer_grid_right,
.footer_grid_left {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 10px;
}

.link_social {
  display: flex;
  gap: 10px;
  text-decoration: none;
  align-items: center;
  padding: 5px 5px;
  position: relative;
}

.link_social::before {
  content: " ";
  display: block;
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #ff9b55;
  top: calc(100% - 2px);
  transform: scaleX(0);
  transition: 0.3s linear;
}
.link_social::after {
  content: " ";
  display: block;
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #ff9b55;
  top: 100%;
  transform: scaleX(0);
  transition: 0.3s linear;
}

.link_social:hover::before,
.link_social:hover::after {
  transform: scaleX(1);
  transition: 0.2s linear;
}

.creator{
  display : flex ; 
  flex-direction: column;
  align-items: center;
  padding-block-start: 50px;
  justify-content: center;
}

.creator_links{
  display : grid ; 
  width : 100% ; 
  padding : 1rem 1rem;
  gap : 20px;
  grid-template-columns: repeat(auto-fit, minmax(200px,1fr));
  justify-items: center;
}

.leetcode_logo{
  width : 20px ; 
  height : 20px;
}

@media screen and (max-width: 768px) {
  .subscribe {
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 992px) {
  .footer_main_inner_container {
    grid-template-columns: repeat(4, 1fr);
  }
  .footer_grid:nth-child(2) {
    grid-row: 1/2;
    grid-column: 1/5;
  }

  .footer_grid:nth-child(1) {
    grid-column: 1/3;
  }

  .footer_grid:nth-child(3) {
    grid-column: 3/5;
  }
}