.strength_main_container {
  width: 100%;
  overflow: hidden;
  padding: var(--paddng_type-2);
  background-color: #161718;
}

.strength_main_inner_container {
  width: 100%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.strength_bottom_container {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  padding: 3rem 0rem;
  gap: 20px;
  padding-inline: 20px;
}

.strength_content_grid {
  padding: 2rem 2rem;
  background-color: #121111;
  font-family: "Forum";
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 10px;
  cursor: grabbing;
}

.strength_image_container {
  animation: scale_rotate_reverse 0.5s linear forwards;
}

.strength_content_grid:hover .strength_image_container {
  animation: scale_rotate 0.5s linear forwards;
}

@keyframes scale_rotate {
  0% {
    transform: scale(1) rotate(0deg);
  }
  50% {
    transform: scale(0) rotate(180deg);
  }
  100% {
    transform: scale(1) rotate(360deg);
  }
}

@keyframes scale_rotate_reverse {
  0% {
    transform: scale(1) rotate(360deg); /* Start from the end of the forward animation */
  }
  50% {
    transform: scale(0) rotate(180deg);
  }
  100% {
    transform: scale(1) rotate(0deg); /* Return to initial state */
  }
}

@media screen and (max-width: 1024px) {
  .strength_bottom_container {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 768px) {
  .strength_bottom_container {
    grid-template-columns: repeat(1, 1fr);
  }
}
