.special_dish_container {
  width: 100%;
  height: auto;
  background-color: #0e0d0c;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.special_1 {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.special_1 img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.special_2 {
  width: 100%;
  height: 100%;
  padding: 0px 4rem;
  position: relative;
  background-image: url("/src/assets/images/shape-4.png");
  background-repeat: no-repeat;
  background-position: right center;
  padding-top: var(--padding_top_type_1);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.special_2_inner_container {
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.special_heading_top {
  width: max-content;
  position: relative;
}

.badges_1 {
  position: absolute;
  top: 10%;
  right: 105%;
}

.special_heading_top .royalty {
  height: 20px;
}

.special_heading_top .anims_block {
  background-position: left;
}

.special_2_heading .heading_type-2 {
  padding-left: 0;
}

.special_2_desc .font-type_2 {
  line-height: 1.3;
}

.special_dish_price {
  padding: 2rem 0;
  display: inline-block;
}

.previous_price {
  display: inline-block;
  font-size: 1.8rem;
  color: gray;
  padding-right: 30px;
}
.current_price {
  font-size: 2.5rem;
  color: #f99b55;
  display: inline-block;
}

.special_dish_button {
  padding: 2rem 0;
  overflow: hidden;
}

@media screen and (max-width: 1200px) {
  .special_2 {
    padding-left: 60px;
  }
}

@media screen and (max-width: 992px) {
  .special_dish_container {
    grid-template-columns: repeat(1, 1fr);
    text-align: center;
  }
  .special_2 {
    padding: 50px 0px;
    padding-inline: 16px;
  }

  .special_dish_button_container {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .special_heading_top .anims_block {
    background-position: center;
  }
}
