.story_main_container {
  width: 100%;
  height: auto;
  background-color: #161718;
  overflow: hidden;
  padding-inline: 20px;
  padding: 5rem 5rem;
}

.story_inner_container {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 80px;
  padding: 5rem 0rem;
}

.story_content_left {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  text-align: center;
}

.story_desc {
  padding: 0.8rem 0rem;
}

.story_content_left .royalty {
  height: 15px;
}

.story_content_left .font-type_2 {
  line-height: 1.4;
}

.story_booking {
  padding: 1rem 0;
}

.story_booking h4 {
  font-size: 1.1rem;
  font-weight: 800;
  padding-bottom: 1rem;
}

.mobile_number {
  font-size: 32px;
  font-family: "roboto";
  font-weight: 900;
  letter-spacing: 3px;
  word-spacing: 5px;
  color: #ff9b55;
}

.story_content_right_inner_container {
  width: 100%;
  height: 100%;
  position: relative;
  padding: var(----padding_type-3);
}

.story_image_1 {
  position: relative;
  width: 100%;
  height: 100%;
  margin-left: auto;
  cursor: pointer;
}

.story_image_1 .back_pattern {
  z-index: -1;
  position: absolute;
  top: -5%;
}

.story_image_1:hover .back_pattern {
  transform: rotateY(180deg);
}

.story_image_1 img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.story_image_2 {
  position: absolute;
  width: 40%;
  height: 53%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  bottom: -10%;
  left: -15%;
  cursor: pointer;
}

.story_image_2 img {
  width: 100%;
  height: 80%;
  object-fit: cover;
  z-index: 2;
}

.story_image_2:hover .back_pattern {
  transform: rotateY(180deg);
  transition: 0.3s linear;
}
@media screen and (max-width: 992px) {
  .story_main_container {
    padding: 5rem 0rem;
    padding-inline: 20px;
  }
  .story_inner_container {
    grid-template-columns: 1fr;
  }
  .story_content_right_inner_container {
    padding: 0rem 0rem 0rem 5rem;
  }

  .story_image_1 {
    max-width: 700px;
    justify-items: right;
    margin-left: auto;
  }

  .story_image_2 {
    left: 0%;
    max-width: 250px;
  }

  .mobile_number {
    font-size: 20px;
  }

  .story_inner_container {
    padding: 2rem 0rem;
  }
}
