.drop_down_main_container {
    width: max(80%, 250px);
    padding-block: 20px;
    padding-inline: 16px;
    min-height: 600px;
    height: 100vh;
    position: fixed;
    top: 0;
    left: -100%;
    bottom: 0;
    z-index: 100;
    overflow-y: scroll;
    background-color: #161718;
    transition: 0.5s ease;
  }

  .drop_down_main_container.active {
    left: 0%;
    transition: 0.5s ease;
  }

  .drop_down_cancel_button {
    height: 50px;
    width: 50px;
    position: absolute;
    top: 3%;
    right: 3%;
    cursor: pointer;
  }

  .back_arrow {
    width: 100%;
    height: 100%;
    color: #ff9b55;
    object-fit: cover;
  }
  .drop_down_inner_container {
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
  }

  .drop_down_header_logo {
    padding-block: 50px;
  }

  .drop_down_list {
    width: 100%;
    padding-inline: 15px;
  }

  .drop_down_links {
    width: 80%;
    display: flex;
    align-items: center;
    text-decoration: none;
    gap: 15px;
    font-size: 20px;
    padding-block: 10px;
    margin-block-start: 5px;
    /* border : 2px solid white; */
    border-bottom-color: #ff9b55;
    border-bottom-style: dashed;
    border-bottom-width: 2px;
  }

  .drop_down_bottom {
    width: 80%;
    display: flex;
    align-self: flex-start;
    flex-direction: column;
    padding-block-start: 50px;
  }

  .drop_down_inner_container .rotating_disc {
    margin-block: 20px;
    width: 80%;
    align-self: flex-start;
  }

  .booking {
    width: 80%;
    align-self: flex-start;
  }

  .phone_number {
    font-size: 36px;
    font-family: "Forum";
  }