.button_type_1 {
    width: max-content;
    padding: 1rem 1.5rem;
    border: 2px solid #ff9955;
    cursor: pointer;
    z-index: 10;
    position: relative;
    overflow: hidden;
  }

  .button_type_1::after {
    content: " ";
    display: block;
    position: absolute;
    width: 200%;
    height: 200%;
    border-bottom-left-radius: 50%;
    border-bottom-right-radius: 50%;
    background-color: #ff9955;
    bottom: 100%;
    left: -50%;
    z-index: -1;
    transition: 0.6s linear;
  }

  .button_type_1:hover::after {
    bottom: -40%;
    transition: 0.4s linear;
  }

  .button_type_1:hover {
    border-color: white;
  }

  .button_type_1_inner {
    position: relative;
    overflow: hidden;
  }

  .button_type_1_text_bottom {
    width : 100%;
    position: absolute;
    top: 100%;
    left: 0;
    color: white;
    text-align: center;
  }

  .button_type_1_text_top {
    color: #ff9955;
  }

  .button_type_1_anim {
    display: inline-block;
    font-size: 1.2rem;
    font-weight: bold;
  }