.reservation_main_container {
  width: 100%;
  overflow: hidden;
  background-color: #0E0D0C;
}
.reservation_top_image_container {
  width: 100%;
  padding: 100px 20px 320px 20px;
  height: auto;
  background-image: url("/src/assets/images/testimonial-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}

.quote {
  font-size: 50px;
  line-height: 0.5;
}

.reservation_heading {
  max-width: 1200px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.rotating_rect {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.rotate_rect {
  background-image: url("data:image/svg+xml,%3c?xml%20version='1.0'%20encoding='UTF-8'%20standalone='no'?%3e%3c!--%20Created%20with%20Inkscape%20(http://www.inkscape.org/)%20--%3e%3csvg%20width='40.439507mm'%20height='57.793861mm'%20viewBox='0%200%2040.439506%2057.793862'%20version='1.1'%20id='svg1'%20xmlns='http://www.w3.org/2000/svg'%20xmlns:svg='http://www.w3.org/2000/svg'%3e%3cdefs%20id='defs1'%20/%3e%3crect%20style='fill:%23ff9955;stroke:%23ffffff;stroke-width:0'%20id='rect1'%20width='33.780945'%20height='36.756161'%20x='18.378084'%20y='-18.378077'%20ry='0'%20rx='0'%20transform='matrix(0.5733082,0.8193398,-0.5733082,0.8193398,0,0)'%20/%3e%3c/svg%3e");
  width: 30px;
  height: 50px;
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
  animation: contineous_rotate 2s linear infinite;
}

@keyframes contineous_rotate {
  0% {
    transform: rotateY(0deg);
  }
  100% {
    transform: rotateY(360deg);
  }
}

.block_image_container {
  padding-top: 1rem;
  cursor: pointer;
}

.block_image {
  width: 100px;
  height: 100px;
  margin: auto;
}

.block_image img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
}

.reservation_bottom_booking_container {
  margin-left: auto;
  margin-right: auto;
  width: 90%;
  margin-block-start: -270px;
  background-color: #0e0d0c;
  display: grid;
  grid-template-columns: 1fr 0.5fr;
}

.reservation_bottom_left_form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 5rem 2rem;
}

.reservation_form {
  width: 100%;
  padding-top: 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  gap: 20px;
}

.input_wrapper_1 {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}

.input_wrapper_2 {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

@media screen and (max-width: 992px) {
  .input_wrapper_2 {
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 768px) {
  .input_wrapper_1 {
    grid-template-columns: 1fr;
  }
}

.selections {
  width: 100%;
  height: 50px;
  background-color: #1b1c1d;
  border: 1px solid white;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.selection_field {
  width: 100%;
  height: 100%;
  border: none;
  background-color: transparent;
}

.inputs_type_1 {
  width: 100%;
}

input {
  width: 100%;
  height: 50px;
  border: 1px solid white;
  box-sizing: border-box;
  background-color: #1b1c1d;
  padding: 1rem 1rem;
  outline: none;
  color: white;
  font-size: 1.3rem;
  font-family: "Forum";
  letter-spacing: 1px;
  transition: 0.2s linear;
}

input:focus-visible {
  border: 2px solid #ff8b55;
  transition: 0.2s linear;
}

input::placeholder {
  color: white;
  font-size: 1.2rem;
}

select {
  outline: none;
}

select {
  color: white;
  font-size: 16px;
}

select::selection .selections {
  flex-direction: row-reverse;
}

option {
  color: white;
  background-color: #1b1c1d;
  padding: 1rem 1rem;
  height: 50px;
  font-size: 18px;
  font-family: "Forum";
}

.profile {
  height: 100%;
}
.profile img {
  height: 100%;
  object-fit: contain;
  padding: 5px 5px;
}

.message {
  width: 100%;
}

.message_textfield {
  width: 100%;
  background-color: #1b1c1d;
  resize: none;
  border: 1px solid white;
  outline: none;
  padding: 1rem 1rem;
  font-size: 20px;
  font-family: "Forum";
  color: white;
  letter-spacing: 1px;
  word-spacing: 2px;
  line-height: 1.2;
}

.reservation_bottom_right_location {
  background-image: url("/src/assets/images/form-pattern.png");
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
  padding: 2rem 0rem;
}

.reservation_bottom_right_container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.reservation_confirm_button {
  width: 100%;
}

.reservation_confirm_button .button_type_1 {
  width: 100%;
  flex: 1;
}

.reservation_confirm_button .button_type_1_text_bottom {
  background-color: yellow;
}

.tal_ce {
  text-align: center;
}

.location {
  text-align: center;
}

.font-type_4 {
  font-size: 1.2rem;
  color: rgb(126, 126, 126);
}

@media screen and (max-width: 992px) {
  .reservation_bottom_booking_container {
    grid-template-columns: 1fr;
  }
  .reservation_heading .font_type-4 {
    font-size: 1.8rem;
  }
}
