.hero_container{
  width : 100%;
  overflow : hidden ; 
}

.hero_inner_container {
  position: relative;
  min-width: 100%;
  height: 55vw;
  overflow: hidden;
}

.hero_slider_container {
  overflow: hidden;
  position: relative;
  background-color: #0e0d0c;
}


.table_book{
  position : absolute ; 
  z-index : 10;
  width : 100px;
  height : 100px;
  right : 10%;
  bottom : 5%;
  
  padding : 1rem;
}

.table_book img{
  width : 100% ; 
  height : 100% ; 
  z-index : 100;
  background-color: #FF9B55;;
  object-fit: cover;
  z-index : 100;

}

.table_back{
  width : 100% ; 
  height : 100% ; 
  background-color: #FF9B55;
  top : 0 ; 
  left : 0 ; 
  z-index : -1;
  position : absolute;
  animation : animation_1 10s linear infinite;
}

.table_back::after{
  width : 100%  ;
  height : 100% ;
  content : ' ' ; 
  top : 0 ; 
  left : 0 ;
  display : block ; 
  position: absolute;
  background-color: transparent;
  border : 2px solid #FF9B55;
  transform : rotate(45deg); 
  animation : animation_2 10s linear infinite ; 
}

@keyframes animation_1 {
  0%{
    transform : rotate(360deg);
  }
  100%{
    transform : rotate(0deg);
  }
}

@keyframes animation_2 {
  0%{
    transform : rotate(45deg);
  }
  100%{
    transform : rotate(45deg);
  }
  
}

