.hero_slider_inner_container {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0%;
  left: 0%;
  transform-origin: center;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  transition: 0.5s linear;
  z-index: 1;
  text-align: center;
  scroll-behavior: smooth;
}

.hero_slider_inner_container.active {
  visibility: visible;
  opacity: 1; /* Fade in */
}

.hero_slider_image_container {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: -1;
  transform: scale(1.15); /* Start at scale 1 */
}

.hero_slider_image_container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero_slider_inner_container.active .hero_slider_image_container {
  animation: smoothScale 7s linear forwards;
}

.hero_slider_content_container {
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-direction: column;
  word-spacing: nowrap;
  padding: 7% 6%;
}

@media screen and (max-width: 768px) {
  .hero_slider_content_container {
    padding: 16% 5%;
  }
}

.hero_slider_content_header_small {
  padding: 0rem 0;
  opacity: 0;
  font-size: 1.5rem;
  transform: translateY(0%);
  transition: 0.2s linear;
}

.hero_slider_inner_container.active .hero_slider_content_header_small {
  transform: translateY(-20%);
  opacity: 1;
  transition: 0.5s 1s linear;
}

.hero_slider_content_header {
  padding-top: var(--padding_top_type_1);
  text-transform: capitalize;
  opacity: 1;
}

.hero_slider_content_header_top,
.hero_slider_content_header_bottom {
  opacity: 0;
  transform: translateY(20%);
  transition: 0.5s linear;
}

.hero_slider_inner_container.active .hero_slider_content_header_top {
  transform: translateY(-20%);
  opacity: 1;
  transition: 0.5s 1.3s linear;
}
.hero_slider_inner_container.active .hero_slider_content_header_bottom {
  transform: translateY(-20%);
  opacity: 1;
  transition: 0.5s 1.9s linear;
}

.hero_slider_content_desc {
  font-size: 20px;
  padding-bottom: 2rem;
  line-height: 1.2;
  transform: translateY(20%);
  opacity: 0;
}

.hero_slider_inner_container.active .hero_slider_content_desc {
  transform: translateY(0%);
  opacity: 1;
  transition: 0.5s 2.3s linear;
}

.hero_slider_button {
  opacity: 0;
  transform: translateY(20%);
  transition: 0.5s linear;
}

.hero_slider_inner_container.active .hero_slider_button {
  opacity: 1;
  transform: translateY(0%);
  transition: 0.5s 2.7s linear;
}

@keyframes smoothScale {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.15);
  }
}
