.navbar_main_container {
  background-color: white;
  color: black;
  position: fixed;
  top: 0;
  left: 0;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
}
.navbar_main_container.trans {
  background-color: transparent;
}

#navbar_container{
  transition : translateY(0%);
  transition : .3s linear;
}

.nav_h_adj {
  height: max(7vw, 80px);
}
#navbar_container.transactive {
  transform: translateY(0%);
  background-color: transparent;
  transition: 0.3s linear;
}

#navbar_container.up {
  transform: translateY(0%);
  background-color: #161718;
  transition: 0.3s linear;
}

#navbar_container.down {
  transform: translateY(-100%);
  background-color: transparent;
  transition: 0.3s linear;
}


.navbar_inner_container {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 1rem;
}

.navbar_logo {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  cursor: pointer;
  gap: 10px;
}

.navbar_logo_container {
  position: relative;
  width: min(70px, 5vw);
  min-width: 40px;
  height: 100%;
}

