.navbar_company_name {
  font-size: 40px;
  font-family: "calligraphed";
  font-weight: 900;
  text-transform: capitalize;
  color: #ff9b55;
}

.navbar_content {
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  gap: 10px;
}

.navigation_content {
  display: flex;
  font-size: 1.6rem;
  gap: 5px;
  font-family: "calligraphed";
}

.navigation_content a {
  text-decoration: none;
  color: #ff9b55;
  font-weight: 400;
  gap: 5px;
  /* text-transform: uppercase; */
}

.nav_content {
  position: relative;
  white-space: nowrap;
  padding: 0.4rem 0.3rem;
}

.nav_content:hover::before,
.nav_content:hover::after {
  transform: scaleX(100%);
  transition: 0.2s ease-in;
}

.nav_content::before {
  content: " ";
  display: block;
  width: 100%;
  height: 1px;
  background-color: #ff9b55;
  position: absolute;
  top: calc(100% - 3px);
  left: 0;
  transform: scaleX(0);
  transition: 0.2s ease-out;
}

.nav_content::after {
  content: " ";
  display: block;
  width: 100%;
  height: 1px;
  background-color: #ff9b55;
  position: absolute;
  top: 100%;
  left: 0;
  transform: scaleX(0);
  transition: 0.2s ease-out;
}

.mobile_dropdown_menu {
  /* padding : .5rem .5rem .5rem 1rem; */
  max-width: 100px;
  display: none;
  cursor: pointer;

  /* display : block; */
}

.mobile_dropdown_inner_container {
  display: flex;
  width: 100%;
  align-items: center;
  gap: 5px;
  justify-content: space-between;
  flex-direction: column;
  margin-inline: 10px;
}

.drop_down_bars {
  background-color: #ff9b55;
  height: 5px;
  width: 50px;
  border-radius: 0.2rem;
  transform: scaleX(1);
  transform-origin: left;
  animation: bars 1s ease-in-out alternate infinite;
}

.drop_down_bars:nth-child(2) {
  animation-delay: 500ms;
}

.drop_down_bars:nth-child(3) {
  animation-delay: 250ms;
}

@keyframes bars {
  0% {
    transform: scaleX(0.3);
  }
  50% {
    transform: scaleX(1);
  }
  100% {
    transform: scaleX(0.3);
  }
}

@media screen and (max-width: 768px) {
  .book_a_table_container {
    display: none;
  }
  .navbar_company_name {
    font-size: 25px;
  }
}

@media screen and (max-width: 960px) {
  .nav_content {
    display: none;
  }
  .mobile_dropdown_menu {
    display: block;
  }
}
